.who-we-are {
    background-color: #f3f5f7;
    padding: 35px 0px;
}

.who-we-are .content-title {
    text-align: center;
}

.bio-container {
    display: flex;
}

.bio-container.left-column, .bio-container.right-column {
    width: 50%
}

.bio-container.left-column {
    margin-right: 35px;
}

.bio-container.right-column .bio-content {
    order: 2;
}

.bio-container.left-column .bio-content {
    margin-right: 15px;
    text-align: right;
}

.bio-container.right-column .bio-content {
    margin-left: 15px;
}

.bio-container.left-column .bio-name {
    float: right;
    align-self: flex-end;
}

.bio-content {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.bio-name {
    font-size: 17px;
    font-weight: 500;
    color: #066b98;
}

.bio-text {
    color: #666;
}

.bio-image {
    width: 173px;
    height: 200px;
    min-width: 173px;
}

@media(max-width: 900px) {
    .bio-container {
        margin-right: 0px;
        width: 100% !important;
    }

    .bio-name {
        align-self: auto !important;
    }

    .bio-container.right-column .bio-name {
        text-align: left;
    }

    .bio-container.right-column .bio-text {
        text-align: left;
    }
}